<!-- UpdateLog.vue -->
<template>
	<div class="update-log-wrapper">
		<!-- ÊêúÁ¥¢Ê°Ü -->
		<div class="search-box">
			<div class="image-box">
				<img v-if="searchIconUrl" :src="searchIconUrl" alt="ÊêúÁ¥¢ÂõæÊ†á" />
			</div>
			<input v-model="searchQuery" type="text" class="search-input" placeholder="ÊêúÁ¥¢ÁâàÊú¨Âè∑„ÄÅÊó•ÊúüÊàñÊõ¥Êñ∞ÂÜÖÂÆπ..." />
		</div>
		<!-- ÊªöÂä®Âå∫Âüü -->
		<div ref="scrollContainer" class="scroll-container">
			<!-- Ê¨¢Ëøé‰ø°ÊÅØ -->
			<div v-if="welcomeMessage" class="welcome-section">
				<div class="welcome-content" v-html="welcomeMessage"></div>
			</div>

			<!-- ÁâàÊú¨ÂàóË°® -->
			<div class="version-list">
				<div v-for="(version, index) in filteredVersions" :key="version.id" class="version-card" :class="{ collapsed: version.collapsed }">
					<!-- ÁâàÊú¨Ê†áÈ¢ò -->
					<div class="version-header" @click="toggleVersion(version)">
						<div class="version-info">
							<span class="version-number">{{ version.version }}</span>
							<span class="version-date">{{ version.date }}</span>
						</div>
						<div class="version-toggle">
							<span class="toggle-icon">{{ version.collapsed ? "‚ñ∂" : "‚ñº" }}</span>
							<span class="update-count">{{ version.items.length }} È°πÊõ¥Êñ∞</span>
						</div>
					</div>

					<!-- Êõ¥Êñ∞ÂÜÖÂÆπ -->
					<div v-show="!version.collapsed" class="version-content">
						<ul class="update-items">
							<li v-for="(item, itemIndex) in version.items" :key="itemIndex" class="update-item" v-html="formatUpdateItem(item)"></li>
						</ul>
					</div>
				</div>

				<!-- Êó†ÁªìÊûúÊèêÁ§∫ -->
				<div v-if="filteredVersions.length === 0" class="no-results">
					<div class="no-results-icon">üîç</div>
					<div class="no-results-text">Ê≤°ÊúâÊâæÂà∞ÂåπÈÖçÁöÑÊõ¥Êñ∞ËÆ∞ÂΩï</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script setup>
import { ref, computed, onMounted, onUnmounted } from "vue";
import { whichWayUpdateLog } from "../../updateLog/index.js";
import { whichWayFile } from "../../file.js";

// Props Âíå Emits
const emit = defineEmits(["loaded"]);

// Refs
const scrollContainer = ref(null);
const searchQuery = ref("");
const searchIconUrl = ref("");

// Êõ¥Êñ∞Êó•ÂøóÊï∞ÊçÆ
const welcomeMessage = ref("");
const versions = ref([]);

// ËøáÊª§ÂêéÁöÑÁâàÊú¨
const filteredVersions = computed(() => {
	if (!searchQuery.value.trim()) {
		return versions.value;
	}

	const query = searchQuery.value.trim().toLowerCase();

	return versions.value.filter(version => {
		// Ê£ÄÊü•ÁâàÊú¨Âè∑
		if (version.version.toLowerCase().includes(query)) {
			return true;
		}

		// Ê£ÄÊü•Êó•Êúü
		if (version.date.toLowerCase().includes(query)) {
			return true;
		}

		// Ê£ÄÊü•Êõ¥Êñ∞ÂÜÖÂÆπ
		return version.items.some(item => {
			// ÁßªÈô§ HTML Ê†áÁ≠æÂêéÊêúÁ¥¢
			const text = item.replace(/<[^>]*>/g, "").toLowerCase();
			return text.includes(query);
		});
	});
});

// Ëß£ÊûêÊõ¥Êñ∞Êó•Âøó
function parseUpdateLog(logText) {
	if (!logText) {
		console.warn("[UpdateLog] Êõ¥Êñ∞Êó•Âøó‰∏∫Á©∫");
		return;
	}

	try {
		// ÁßªÈô§ <pre> Ê†áÁ≠æ
		const cleanText = logText
			.replace(/<pre[^>]*>/g, "")
			.replace(/<\/pre>/g, "")
			.trim();

		// ÂàÜÂâ≤ÊàêË°å
		const lines = cleanText.split("\n").map(line => line.trim());

		// Á¨¨‰∏ÄÈÉ®ÂàÜÔºöÊ¨¢Ëøé‰ø°ÊÅØÔºàÁõ¥Âà∞Á¨¨‰∏Ä‰∏™ÁâàÊú¨Âè∑‰πãÂâçÔºâ
		const welcomeLines = [];
		let versionStartIndex = 0;

		for (let i = 0; i < lines.length; i++) {
			// Ê£ÄÊµãÁâàÊú¨Âè∑Ê†ºÂºèÔºöYYYY.MM.DD vX.X.X
			if (/^\d{4}\.\d{1,2}\.\d{1,2}\s+v\d+\.\d+/.test(lines[i])) {
				versionStartIndex = i;
				break;
			}
			if (lines[i]) {
				welcomeLines.push(lines[i]);
			}
		}

		// Â§ÑÁêÜÊ¨¢Ëøé‰ø°ÊÅØ
		if (welcomeLines.length > 0) {
			welcomeMessage.value = welcomeLines.join("<br>");
		}

		// Ëß£ÊûêÂêÑ‰∏™ÁâàÊú¨
		const versionList = [];
		let currentVersion = null;

		for (let i = versionStartIndex; i < lines.length; i++) {
			const line = lines[i].trim();

			// Ê£ÄÊµãÁâàÊú¨Âè∑Ë°å
			const versionMatch = line.match(/^(\d{4}\.\d{1,2}\.\d{1,2})\s+(v\d+\.\d+(?:\.\d+)?)/);
			if (versionMatch) {
				// ‰øùÂ≠ò‰∏ä‰∏Ä‰∏™ÁâàÊú¨
				if (currentVersion) {
					versionList.push(currentVersion);
				}

				// ÂàõÂª∫Êñ∞ÁâàÊú¨
				currentVersion = {
					id: `v${versionList.length + 1}`,
					date: versionMatch[1],
					version: versionMatch[2],
					items: [],
					collapsed: versionList.length > 2, // ÈªòËÆ§ÊäòÂè†ËæÉÊóßÁöÑÁâàÊú¨
				};
				continue;
			}

			// Â¶ÇÊûúÂΩìÂâçÊúâÁâàÊú¨ÔºåÂ§ÑÁêÜÊõ¥Êñ∞È°π
			if (currentVersion && line) {
				// ÁßªÈô§ÂºïÂè∑ÂíåÈÄóÂè∑
				let item = line.replace(/^"/, "").replace(/",?$/, "").trim();

				// Ë∑≥ËøáÁ©∫Ë°å
				if (item) {
					currentVersion.items.push(item);
				}
			}
		}

		// ‰øùÂ≠òÊúÄÂêé‰∏Ä‰∏™ÁâàÊú¨
		if (currentVersion) {
			versionList.push(currentVersion);
		}

		versions.value = versionList;
		console.log("[UpdateLog] Â∑≤Ëß£Êûê", versionList.length, "‰∏™ÁâàÊú¨");
	} catch (error) {
		console.error("[UpdateLog] Ëß£ÊûêÊõ¥Êñ∞Êó•ÂøóÂ§±Ë¥•:", error);
	}
}

// Ê†ºÂºèÂåñÊõ¥Êñ∞È°π
function formatUpdateItem(item) {
	// Â§ÑÁêÜ HTML Ê†áÁ≠æ
	item = item.replace(/&lt;/g, "<").replace(/&gt;/g, ">");

	// È´ò‰∫ÆÂÖ≥ÈîÆËØç
	const highlightPatterns = [
		{ pattern: /Êñ∞Â¢û(Âπ≤Âëò|ÁöÆËÇ§|Âä®ÊÄÅÁöÆËÇ§|ËÉåÊôØ|ÊàêÂ∞±|Ê®°ÁªÑ|ÂäüËÉΩ|Ê®°Âºè)/g, class: "keyword-add" },
		{ pattern: /Ë∞ÉÊï¥(ÊäÄËÉΩ|Ê≠¶Â∞Ü)/g, class: "keyword-modify" },
		{ pattern: /‰øÆÂ§ç.*?ÁöÑbug/g, class: "keyword-fix" },
		{ pattern: /‰ºòÂåñ.*?/g, class: "keyword-optimize" },
		{ pattern: /Âà†Èô§.*?/g, class: "keyword-remove" },
	];

	let result = item;
	highlightPatterns.forEach(({ pattern, class: className }) => {
		result = result.replace(pattern, match => {
			return `<span class="${className}">${match}</span>`;
		});
	});

	return result;
}

// ÂàáÊç¢ÁâàÊú¨Â±ïÂºÄ/ÊäòÂè†
function toggleVersion(version) {
	version.collapsed = !version.collapsed;
}

// ÂàùÂßãÂåñ
onMounted(async () => {
	try {
		// Ëß£ÊûêÊõ¥Êñ∞Êó•Âøó
		if (whichWayUpdateLog.updateLog) {
			parseUpdateLog(whichWayUpdateLog.updateLog);
		} else {
			console.warn("[UpdateLog] Êú™ÊâæÂà∞Êõ¥Êñ∞Êó•ÂøóÊï∞ÊçÆ");
		}

		// Ëé∑ÂèñÊêúÁ¥¢ÂõæÊ†á
		searchIconUrl.value = whichWayFile.compilePath("ui:watch.png");

		// Ê∑ªÂä†ËøîÂõûÈ°∂ÈÉ®ÊåâÈíÆ
		if (scrollContainer.value) {
			//   await configUI.addBackToTopButton({ container: scrollContainer.value });

			// Ëß¶ÂèëÂä†ËΩΩÂÆåÊàê‰∫ã‰ª∂
			emit("loaded");
		}
	} catch (error) {
		console.error("[UpdateLog] ÂàùÂßãÂåñÂ§±Ë¥•:", error);
	}
});

// Ê∏ÖÁêÜ
onUnmounted(() => {
	// ÁªÑ‰ª∂Âç∏ËΩΩÊó∂ÁöÑÊ∏ÖÁêÜÂ∑•‰Ωú
});
</script>

