	margin-bottom: 24px;
	padding: 20px;
	background: linear-gradient(135deg, rgba(64, 158, 255, 0.2), rgba(40, 96, 144, 0.2));
	border-radius: 12px;
	border: 1px solid rgba(64, 158, 255, 0.4);
	position: relative;
	width: calc(100% - 40px);
	height: 200px;
}

